@media only screen and (max-width: 600px) and (min-width: 240px) {
  *{
    box-sizing: border-box;
    scroll-behavior: smooth;
  }

  .root{
    background-color: black;
  }

  .App{
    margin: 0;
    padding: 0;
    color: white;
    height: 100%;
    min-height: 100%;
    background-color: black;
    scroll-behavior: smooth;
  }

  .background{
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../src/images/homebg.jpeg);
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
    filter: brightness(50%);
  }

  .logo{
    border-radius: 0px 10px 0px 10px;
    width: 162px;
    background-image: linear-gradient(to right, rgba(255, 255, 0, 0.350), rgba(255, 255, 0, 0.450));
    color: black;
    padding: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2rem;
    display: flex;
    flex-direction: column;
    line-height: 24px;
    margin: 0;
  }

  .logo-caption{
    font-size: 0.835rem;
  }

  .footer{
    width: 100%;
    background-color: #ffff0075;
    position: absolute;
    bottom: 0px;
    text-align: center;
    z-index: 0;
    margin: 0;
  }

  .footer>p{
    margin: 0px;
  }

  #days{
    font-family: 'PT Serif', serif;
    font-size: 1rem;
  }

  .menubutton{
    float: right;
    margin-bottom: 30px;
    transition: 1s;
    border-radius: 5px;
    background-color: transparent;
    border: 1.5px solid rgba(255, 255, 0, 0.459);
    color: white;
    padding: 6px;
    outline: none;
  }

  .buttons{
    position: absolute;
    top: 40px;
    left: 5%;
  }

  button{
    font-family: 'PT Serif', serif;
  }

  .menu:active, .contact:active, .sendmsg:active, .menubutton:active{
    transform: scale(0.8);
    outline:none;
    background-image: linear-gradient(to right, rgba(255, 255, 0, 0.350), rgba(255, 255, 0, 0.450));
  }

  .menu, .contact, .sendmsg{
    border-radius: 5px;
    background-color: transparent;
    border: 1.5px solid rgba(255, 255, 0, 0.459);
    color: white;
    padding: 8px 20px;
    outline: none;
    transition: 1s;
  }

  .contact{
    margin-top: 20px;
  }

  .address{
    font-size: 0.8rem;
    text-align: center;
  }

  .reservations{
    text-align: center;
    margin-bottom: 0px;
  }

  .workdays{
    position: absolute;
    bottom: 80px;
    line-height: 8px;
    left: 5%;
    z-index: 0;
  }

  .displaymenutext{
    position: absolute;
    color: white;
    top: 30px;
    left: 0;
    right: 0;
    margin: 0 auto;
    height: 88%;
    width: 90%;
    overflow: scroll;
    background-color: black;
    scrollbar-width: none;
    display: none;
    overflow-x: hidden;
  }

  .displaymenutext::-webkit-scrollbar{
    display: none;
  }

  .cartmenu{
    display: none;
    grid-template-rows: 0% 4% 64% 32%;
    row-gap: 30px;
    padding: 30px 2%;
    position: absolute;
    color: white;
    top: 30px;
    left: 0;
    right: 0;
    margin: 0 auto;
    height: 88%;
    max-height: 88%;
    width: 90%;
    overflow: scroll;
    background-color: black;
    scrollbar-width: none;
    overflow-y: hidden;
    z-index: 1;
  }

  #total{
    display: grid;
    grid-template-columns: 84% auto;
  }
  
  hr{
    width: 90%;
  }

  .carthead{
    height: 0px;
  }

  ul{
    padding: 0px 2px;
  }

  ul>li{
    list-style-type: none;
  }

  .carthead>ul{
    display: grid;
    grid-template-columns: 40% 20% 10% 30%;
    text-align: center;
  }

  .ullist{
    overflow-y: scroll;
  }

  .ullist>ul{
    display: grid;
    grid-template-columns: 40% 20% 10% 30%;
  }

  .cover{
    padding: 0;
    text-align: center;
  }

  .displaymenutext>p{
    font-size: 0.9rem;
    color: whitesmoke;
    padding: 5px;
  }

  .foods, .info{
    border-radius: 0px 10px 0px 10px;
    font-weight: 500;
    color: black;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin: 0 auto;
    padding: 10px;
    font-size: 1.6rem;
    background-image: linear-gradient(to right, rgba(255, 255, 0, 0.650), rgba(255, 255, 0, 0.450));
  }

  .food-border{
    width: 100%;
    margin: 0 auto;
  }

  .info{
    margin-top: 30px;
  }

  .contact-details{
    padding: 0px 10px; 
  }

  .food-details{
    padding: 10px 10px;
    margin: 0px;
    color: white;
    font-family: 'PT Serif', serif;
    text-align: center;
  }

  img{
    display: block;
    margin: 0 auto;
    margin-top: 20px;
  }

  textarea{
    width: 100%;
    font-size: 1.1rem;
    padding: 10px;
  }

  .cf{
    width: 100%;
    padding: 10px;
    margin-bottom: 4px;
  }

  .circle{
    border: 2px solid black;
    background-color: white;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    position: absolute;
    top: 5px;
    right: 5px;
    color: black;
    text-align: center;
    font-size: 0.8rem;
  }

  .fa-shopping-cart{
    color: rgba(255, 255, 0, 0.459);
    position: absolute;
    top: 60%;
    left: 20px;
    transform: translate(-50%, -50%);
    font-size: 1.5rem;
  }

  .cartblock{
    position: absolute;
    top: 40px;
    right: 30px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 1.5px solid rgba(255, 255, 0, 0.459);
    transition: 0.6s;
  }

  .cartblock:active{
    transform: scale(0.8)
  }

  .menublock, .contactblock{
    display: none;
    width: 88%;
    transition: 1s;
  }

  .menublock{
    margin: 10px auto;
  }

  .contactblock{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  input:focus, textarea:focus{
    outline: none;
  }

  .close{
    width: 10px;
    font-size: 2rem;
    position: sticky;
    top: 0px;
    font-weight: bolder;
  }

  .qty{
    width: 24px;
    text-align: center;
    outline: none;
    border-radius: 2px;
  }
}

@media only screen and (max-width: 330px) {
  .carthead>ul{
    grid-template-columns: 30% 21% 13% 36%;
  }

  .ullist>ul{
    grid-template-columns: 30% 21% 13% 36%;
  }
}

@media only screen and (min-width: 601px) {
  .App{
    display: none;
  }

  body{
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 3rem;
    background-color: rgba(255, 255, 0, 0.459);
  }

  body::before{
    content: "MOBILE VERSION ONLY.";
  }

  body::after{
    content: "DESKTOP VERSION COMING SOON.";
  }
}